import OpenAI from 'openai';

const openai = new OpenAI({
  apiKey: process.env.REACT_APP_OPENAI_API_KEY,
  dangerouslyAllowBrowser: true
});

// Course information for recommendations
const FUNCTIONAL_BASICS_INFO = `
FUNCTIONAL BASICS - BASKURS INOM FUNCTIONAL FOODS
- 6 veckor, 2295 kr
- 75 recept och mltidsplaner
- Fokus p grundlggande hlsosam mat och longevity
- Passar dig som vill: frbttra allmn hlsa, mer energi, bttre matvanor, starkare immunfrsvar
- Hlsovinster: stabilt blodsocker, bttre matsmltning, mindre inflammation, hormonell balans
- Bra fr: variabel/lg energi, fokusproblem, dliga matvanor, svagt immunfrsvar, allmn hlsa
`;

const FUNCTIONAL_FLOW_INFO = `
FUNCTIONAL FLOW - MAGHLSA OCH ANTIINFLAMMATORISK KOST  
- 6 veckor, 2295 kr
- 85 recept specialiserade p mage-tarmhlsa
- Fokus p maghlsa, antiinflammatorisk kost, tarmflora
- Passar dig som har: magproblem, uppblsthet, energitrtthet, inflammationer
- Hlsovinster: frbttrad tarmflora, minskad inflammation, bttre matsmltning, jmnare energi
- Bra fr: magbesvr, matsmltningsproblem, energidippar, inflammationer, tarmhlsa
`;

const translateAnswers = (answers) => {
  const answerMap = {
    // Energy levels
    'high_energy': 'Hg energi genom hela dagen',
    'afternoon_dip': 'Bra energi men trtthet p eftermiddagen',
    'variable_energy': 'Varierande energi under dagen',
    'low_energy': 'Lg energi och konstant trtthet',
    
    // Sleep
    'excellent_sleep': 'Utmrkt smn (7-9 timmar, vaknar utvilad)',
    'good_sleep': 'Bra smn men vaknar ibland under natten',
    'disrupted_sleep': 'Svrt att somna eller vaknar ofta',
    'poor_sleep': 'Dlig smn (fr lite eller dlig kvalitet)',
    
    // Stress
    'low_stress': 'Hanterar stress mycket bra',
    'moderate_stress': 'Mttlig stress, klarar det mesta',
    'high_stress': 'Ofta stressad och vervldigad',
    'chronic_stress': 'Konstant stress och ngest',
    
    // Exercise
    'very_active': 'Trnar 5+ gnger per vecka',
    'active': 'Trnar 3-4 gnger per vecka',
    'somewhat_active': 'Trnar 1-2 gnger per vecka',
    'sedentary': 'Trnar sllan eller aldrig',
    
    // Diet
    'excellent_diet': 'Mycket hlsosam och balanserad kost',
    'good_diet': 'Ganska hlsosam men kan frbttras',
    'mixed_diet': 'Blandat - vissa mltider hlsosamma',
    'poor_diet': 'Ohlsosam kost med mycket processad mat',
    
    // Digestion
    'excellent_digestion': 'Utmrkt matsmltning - inga problem',
    'occasional_issues': 'Mest bra med tillflliga problem',
    'frequent_issues': 'Regelbundna magproblem',
    'chronic_issues': 'Konstanta matsmltningsbesvr',
    
    // Immunity
    'strong_immunity': 'Sllan eller aldrig sjuk',
    'normal_immunity': 'Blir sjuk 1-2 gnger per r',
    'frequent_illness': 'Blir sjuk 3-4 gnger per r',
    'weak_immunity': 'Blir mycket ofta sjuk',
    
    // Focus
    'excellent_focus': 'Utmrkt fokus och mental klarhet',
    'good_focus': 'Bra fokus men trtthet pverkar',
    'poor_focus': 'Svrt att koncentrera sig',
    'brain_fog': 'Stndig hjrndimma och frvirring',
    
    // Health challenges
    'weight_management': 'Vikthantering',
    'energy_fatigue': 'Energi och trtthet',
    'stress_recovery': 'Stress och terhmtning',
    'general_health': 'Allmn hlsa och prevention',
    
    // Goals
    'energy_vitality': 'Mer energi och vitalitet',
    'immunity_boost': 'Bttre immunfrsvar',
    'digestive_health': 'Frbttrad matsmltning',
    'mental_clarity': 'Mental klarhet och fokus'
  };
  
  return Object.keys(answers).map(key => answerMap[answers[key]] || answers[key]).join(', ');
};

export const generatePersonalizedRecommendations = async (quizData) => {
  try {
    const translatedAnswers = translateAnswers(quizData);
    
    const prompt = `Du r Ulrika Davidsson, kostrdgivare, receptkreatr och Nordens ledande expert p functional foods. Du har hjlpt tusentals mnniskor till hlsosamma livsstilsfrndringar genom din unika metod som kombinerar nringslra med praktisk vardagsplanering.

Din filosofi bygger p:
- Functional foods: Naturliga livsmedel med specifika hlsofrmjande egenskaper
- Mervrdesmat rik p antioxidanter, probiotika, prebiotika och omega-3
- Hllbara livsstilsfrndringar genom disciplin, motivation och smart planering
- Nringsrika, lttlagade och goda recept fr hela familjen
- Holistisk approach dr maten r rtt brnsle fr kroppen

Baserat p fljande svar frn en kund, skapa detaljerade och personaliserade rekommendationer enligt din metod p svenska:

KUNDENS SVAR: ${translatedAnswers}

Skapa rekommendationer inom dessa fem kategorier:

1. KOSTRD & NRING (Enligt Ulrikas metod)
- Specifika functional foods att prioritera baserat p personens behov
- Nringsrika livsmedel med antioxidanter, probiotika och omega-3
- Mltidsplanering fr optimal nringsupptag och energibalans  
- Praktiska inkpslistor och vardagstips
- Anti-inflammatoriska ingredienser fr denna persons specifika utmaningar

2. LIVSSTILSREKOMMENDATIONER (Ulrikas holistiska approach)
- Smnoptimering fr hormonal balans och terhmtning
- Stresshantering genom kostval och mindfulness
- Trningsrd som kompletterar kostplanen
- Dagliga rutiner fr hllbar livsstilsfrndring
- Hur man skapar nya, bestende vanor

3. FUNCTIONAL FOODS & MERVRDESMAT
- Specifika functional foods fr denna persons hlsoprofil
- Antioxidantrika superfoods och deras effekter
- Probiotika och prebiotika fr optimal tarmhlsa  
- Omega-3-kllor och anti-inflammatoriska kryddor
- Praktiska stt att integrera functional foods i vardagen

4. PRIORITERINGAR & GENOMFRANDE
- De 3 viktigaste omrdena enligt Ulrikas metod
- Varfr dessa r avgrande fr denna persons hlsoml
- Steg-fr-steg plan fr de frsta veckorna
- Disciplin och motivation fr bestende frndring

5. KURSREKOMMENDATION
Baserat p kundens svar, rekommendera den mest passande kursen och frklara varfr:

${FUNCTIONAL_BASICS_INFO}

${FUNCTIONAL_FLOW_INFO}

Vlj den kurs som bst matchar kundens behov enligt Ulrikas metod och frklara:
- Varfr just denna kurs passar personens hlsoprofil och livssituation
- Vilka specifika functional foods och tekniker de kommer lra sig
- Hur Ulrikas beprvade metod kommer transformera deras vardag
- Konkreta, mtbara resultat de kan frvnta sig inom 6 veckor
- Personlig coachning och community-std som ingr

Skriv som Ulrika sjlv - varmt, kunnigt och inspirerande. Anvnd hennes erfarenhet av att ha hjlpt tusentals mnniskor. Formatera svaret som HTML med <h3> fr underrubriker, <p> fr text, <ul> och <li> fr listor, och <strong> fr viktiga punkter. Ge konkreta, genomfrbara rd baserat p functional foods-principerna.`;

    const completion = await openai.chat.completions.create({
      model: "gpt-4o-mini",
      messages: [
        {
          role: "system",
          content: "Du r Ulrika Davidsson - kostrdgivare, receptkreatr och frfattare till ver 30 kokbcker. Du r Nordens ledande expert p functional foods och har hjlpt tusentals mnniskor med din 'Kickstart'-metod. Svara alltid p svenska med din varma, uppmuntrande och kunniga rst. Ge konkreta, praktiska rd baserat p functional foods-principerna och din beprvade erfarenhet."
        },
        {
          role: "user",
          content: prompt
        }
      ],
      max_tokens: 4000,
      temperature: 0.7
    });

    const fullResponse = completion.choices[0].message.content;
    
    // Split the response into sections
    const sections = fullResponse.split(/(?=<h3>)/);
    
    const result = {
      diet: sections.find(s => s.includes('KOSTRD') || s.includes('NRING')) || sections[1] || '',
      lifestyle: sections.find(s => s.includes('LIVSSTIL')) || sections[2] || '',
      supplements: sections.find(s => s.includes('FUNCTIONAL') || s.includes('TILLSKOTT')) || sections[3] || '',
      priorities: sections.find(s => s.includes('PRIORITER')) || sections[4] || '',
      courseRecommendation: sections.find(s => s.includes('KURSREKOMMENDATION') || s.includes('KURS')) || sections[5] || ''
    };

    return result;
    
  } catch (error) {
    console.error('Error calling OpenAI API:', error);
    throw new Error('Kunde inte generera rekommendationer. Frsk igen senare.');
  }
}; 